<template>
    <div>
        <van-nav-bar title="客户订单" left-arrow @click-left="onClickLeft"/>

       <div v-if="info.length!=0">
                      <div class="crd" v-for="item in info" v-if="item.status==2">
                 <router-link :to="{name:'ord', params:{id:item.order_no}}">                                                       
             <ul>                  
                 <li>
                     <div>
                     <img src="./../../assets/i/home/1.png" alt=""><span>{{item.consignee}}</span>
                     </div>
                     <small>{{item.create_time}}</small>
                     <strong>待审核</strong>
                 </li>
                 <li>
                     <!-- <p>混批</p> -->
                     <h4>{{item.goods_num}}件商品</h4>
                     <p>￥{{item.price}}</p>
                 </li>
                 <li>
                     <em>订单号：{{item.order_no}}</em>
                     <div class="stat">
                         <span @click.prevent="adopt(item.order_no)">通过</span>
                         <span @click.prevent="reject(item.order_no)">驳回</span>                         
                     </div>
                 </li>
             </ul>
                 </router-link>             
         </div>
                  <div class="crd" v-for="item in info" v-if="item.status==1">
                 <router-link :to="{name:'ord', params:{id:item.order_no}}">                                                       
             <ul>                  
                 <li>
                     <div>
                     <img src="./../../assets/i/home/1.png" alt=""><span>{{item.consignee}}</span>
                     </div>
                     <small>{{item.create_time}}</small>
                     <strong style="color:green">待审核中</strong>
                 </li>
                 <li>
                     <!-- <p>混批</p> -->
                     <h4>{{item.goods_num}}件商品</h4>
                     <p>￥{{item.price}}</p>
                 </li>
                 <li>
                     <em>订单号：{{item.order_no}}</em>
                     <div class="stat">                       
                     </div>
                 </li>
             </ul>
                 </router-link>             
         </div>
  
              <div class="crd" v-for="item in info" v-if="item.status==3">
                 <router-link :to="{name:'ord', params:{id:item.order_no}}">                                                                    
             <ul>
                 <li>
                     <div>
                     <img src="./../../assets/i/home/1.png" alt=""><span>{{item.consignee}}</span>
                     </div>
                     <small>{{item.create_time}}</small>
                     <strong style="color:green">已审核</strong>
                 </li>
                 <li>
                     <h4>{{item.goods_num}}件商品</h4>
                     <p>￥{{item.price}}</p>
                 </li>
                 <li>
                     <em>订单号：{{item.order_no}}</em>
                     <div class="stat">
                     </div>
                 </li>
             </ul>
                 </router-link>                          
         </div>
              <div class="crd" v-for="item in info" v-if="item.status==4">
                 <router-link :to="{name:'ord', params:{id:item.order_no}}">                                                                                       
             <ul>
                 <li>
                     <div>
                     <img src="./../../assets/i/home/1.png" alt=""><span>{{item.consignee}}</span>
                     </div>
                     <small>{{item.create_time}}</small>
                     <strong style="color:red">被驳回</strong>
                 </li>
                 <li>
                     <h4>{{item.goods_num}}件商品</h4>
                     <p>￥{{item.price}}</p>
                 </li>
                 <li>
                     <em>订单号：{{item.order_no}}</em>
                     <div class="stat">
                     </div>
                 </li>
             </ul>
                 </router-link>                                       
         </div>
       </div>
       <div v-else='info.length==0'>
           <nodata></nodata>
       </div>
       
    </div>
</template>
<script>
  import Vue from 'vue'
  import {Toast} from 'vant'
  import nodata from '../../components/noData'
  
  Vue.use(Toast);
export default {
    data(){
      return{
          info:[]
      }  
    },
    components:{
      nodata
    },
    methods:{
        onClickLeft(){
            // this.$ruter.push(-1)
            this.$router.back(-1)
        },
        reject(data){
            this.apiPost('AuditOrder/rejected_order',{
                order_no:data,
                token:localStorage.getItem('token')
            }).then(res=>{
                // console.log('驳回',res)
                if(res.data.code==200){
                      Toast(res.data.message)
                    //  this.$router.push('order')
                      location.reload()
                }else{
                    Toast(res.data.message)
                }
               
            })    
        },
        adopt(data){
            this.apiPost('AuditOrder/audit_order',{
                order_no:data,
                token:localStorage.getItem('token')
            }).then(res=>{
                // console.log('TG',res.data.message)
                if(res.data.code==200){
                    Toast(res.data.message)
                    location.reload()
                }else{
                    Toast(res.data.message)
                }
            }).catch(err=>{
                Toast('发生错误：'+err)
            })
        }
    },
    mounted(){
        this.apiGet('AuditOrder/user_order',{
            token:localStorage.getItem('token')
        }).then(res=>{
            // console.log(res.data.data,11)
            this.info=res.data.data
        })
    }
}
</script>
<style scoped>
    .crd{
        /* color: gr; */
        margin: 1em;
        background-color: #fff;
        padding: 0 1em;
        font-size: 13px;
        border-radius: .5em;
        box-shadow: 0 0 .5em .1em #ccc;
    }
    .crd ul li{
        text-align: left;
        display:flex;
        justify-content: space-between;
        padding: 1em 0;
        border-bottom: 1px solid #f8f8f8;
        color: #000;
    }
    .crd ul li img{
        width: 1em;
        vertical-align: middle;
        margin: 0 .5em;
    }
    .crd ul li span{
        vertical-align: middle;
    }
    .crd ul li em{
        font-style: normal;
        font-size: 12px;
        color: #ccc;
    }
    small{
        font-size: 12px;
        color: #ccc;
    }
    .stat span{
        padding: .3em 1em;
        background-color: #b43e3a;
        color: white;
        margin: 0 .3em;
        border-radius: 1em;
    }
    .stat span:first-child{
        background: green;
    }
</style>



