<template>
    <div>
        <van-nav-bar title="我的订单" left-arrow @click-left="onClickLeft"/>
    
        <van-tabs v-model="active">
            <van-tab v-for="(items,index) in list" :title='items' :key=index>
    <div v-if="info.length!=0">
           <div class="crd"  v-for="item in info" >
                 <router-link :to="{name:'ord', params:{id:item.order_no}}">                                                                               
             <ul>
                 <li>
                     <div>
                     <img src="./../../assets/i/home/1.png" alt=""><span>{{item.consignee}}</span>
                     </div>
                     <small>{{item.create_time}}</small>
                     <strong>
                        <div v-if="item.status==1"> 
                          总代{{items}}
                        </div>
                        <div v-else-if="item.status==2"> 
                          后台{{items}}
                        </div>
                        <div v-else>
                          {{items}}
                        </div>
                     </strong>
                 </li>
                 <li> 
                     <h4>{{item.goods_num}}件商品</h4>
                     <p>￥{{item.price}}</p>
                 </li>
                 <li>
                     <em>订单号：{{item.order_no}}</em>
                     <div class="stat">
                     </div>
                 </li>
             </ul>
                 </router-link>                                                    
         </div>
    </div>
    <div v-if="info.length==0">
        <nodata></nodata>
    </div>
     

  </van-tab>
        </van-tabs>

    </div>
</template>
<script>
import Vue from 'vue';
import { Tab, Tabs, Toast} from 'vant';
import nodata from '../../components/noData'
Vue.use(Tab).use(Tabs).use(Toast);
export default {
    data(){
        return{
            active:this.$route.params.index,
            info:[],
            list:['待审核','已审核','被驳回']
        }
    },
    components:{
        nodata
    },
    watch:{
        active(res){
            this.apiPost('AuditOrder/my_order',{
            token:localStorage.getItem('token'),
            status:res
        }).then(res=>{
            if(res.data.code==200){
                this.info=res.data.data
            }else{
                Toast(res.data.message)
            }
            
        })
        }
    },
    methods:{
        onClickLeft(){
            this.$router.push('/home')
        }
    },
    mounted(){
        this.apiPost('AuditOrder/my_order',{
            token:localStorage.getItem('token'),
            status:this.$route.params.index
        }).then(res=>{
            if(res.data.code==200){
            this.info=res.data.data
                
            }else{
                Toast(res.data.message)
            }
        })
    }
}
</script>
<style scoped>
  .crd{
        margin: 1em;
        background-color: #fff;
        padding: 0 1em;
        font-size: 13px;
        border-radius: .5em;
        box-shadow: 0 0 0.1em 0.1em #ccc;
    }
    .crd ul li {
        text-align: left;
        display:flex;
        justify-content: space-between;
        padding: 1em 0;
        border-bottom: 1px solid #f8f8f8;
        color: #000;
    }
    .crd ul li img{
        width: 1em;
        vertical-align: middle;
        margin: 0 .5em;
    }
    .crd ul li span{
        vertical-align: middle;
    }
    .crd ul li em{
        font-style: normal;
        font-size: 12px;
        color: #ccc;
    }
    small{
        font-size: 12px;
        color: #ccc;
    }
    .stat span{
        padding: .3em 1em;
        background-color: #b43e3a;
        color: white;
        margin: 0 .3em;
        border-radius: 1em;
    }
    .stat span:first-child{
        background: green;
    }
</style>





