<template>
    <div>
    <van-nav-bar title="推荐总代月业绩" left-arrow @click-left="onClickLeft"/>
    <!-- <input type="text" name="" id="" placeholder="请输入代理名字或者手机号" > -->
     <div v-if="info.length!=0">
     <div class="agent">
        <ul>
            <li v-for="item in info">
                <div class="agent_l">
                <img src="./../../assets/i/01/02.png" alt="">
                <div>
                    <p>{{item.get_user.name}}</p>
                    <small>{{item.get_user.phone}}</small>
                </div>
                </div>
                <div class="agent_r">
            
                        <p>包数：{{item.sum}}包</p>
                        <span>金额：￥{{item.price}}</span>
                </div>

            </li>
            <!-- //// -->
        </ul>
    </div>
     </div>
     <div v-else='info.length==0'>
            <nodata></nodata>
     </div>

    </div>
</template>
<script>
import Vue from 'vue'
import { NavBar,Toast} from 'vant';
import nodata from '../../components/noData'
Vue.use(NavBar).use(Toast);
export default {
    data(){
        return{
            info:[]
        }
    },
    components:{
        nodata
    },
    methods:{
        onClickLeft(){
            this.$router.back(-1)
        }
    },
    mounted(){
        this.apiPost('team/rec_gen',{
            token:localStorage.getItem('token'),
            start_time:this.$route.params.time            
        }).then(res=>{
            console.log(res.data,'推荐总代月业绩')
            if(res.data.code==200){
                Toast(res.data.message)
                // console.log(1)
                console.log(res.data.data)
                this.info=res.data.data
            }
        })
    }
}
</script>
<style scoped>
.agent ul li{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    background: #fff;
    padding: .5em;
    margin: .3em 0;
}
.agent ul li .agent_l{
    display: flex;
    flex-direction: row;
    text-align: left;
    /* flex-basis: content; */
    
}
.agent ul li .agent_l div{
    display: flex;
    flex-direction: column;
    justify-content: space-around;
}
.agent ul li .agent_l img{
    margin: 0 .5em;
    width: 3em;
    height: 3em;
}
.agent ul li .agent_l small{
    color: #ccc;
}
.agent ul li .agent_r{
    display: flex;
    flex-direction: column;
    text-align: right;
    justify-content: space-around;
}
.agent ul li .agent_r span{
    /* color: red; */
    color: #ccc;
    font-size: .9em;
}

</style>


